.van-address-list {
  box-sizing: border-box;
  height: 100%;
  padding: 12px 12px 100px;
}
.van-address-list__bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  box-sizing: border-box;
  width: 100%;
  padding: 5px 16px;
  background-color: #fff;
}
.van-address-list__add {
  height: 40px;
  line-height: 38px;
}
.van-address-list__disabled-text {
  padding: 20px 0 16px;
  color: #969799;
  font-size: 14px;
  line-height: 20px;
}
.van-address-item {
  padding: 12px;
  background-color: #fff;
  border-radius: 8px;
}
.van-address-item:not(:last-child) {
  margin-bottom: 12px;
}
.van-address-item__value {
  padding-right: 44px;
}
.van-address-item__name {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 22px;
}
.van-address-item__tag {
  flex: none;
  margin-left: 8px;
  padding-top: 0;
  padding-bottom: 0;
  line-height: 1.4em;
}
.van-address-item__address {
  color: #323233;
  font-size: 13px;
  line-height: 18px;
}
.van-address-item--disabled .van-address-item__name,
.van-address-item--disabled .van-address-item__address {
  color: #c8c9cc;
}
.van-address-item__edit {
  position: absolute;
  top: 50%;
  right: 16px;
  color: #969799;
  font-size: 20px;
  transform: translate(0, -50%);
}
.van-address-item .van-cell {
  padding: 0;
}
.van-address-item .van-radio__label {
  margin-left: 12px;
}
.van-address-item .van-radio__icon--checked .van-icon {
  background-color: #f56a5d;
  border-color: #f56a5d;
}
