// Basic Colors
@black: #000;
@white: #fff;
@red: #f56a5d;
// @blue: #1989fa;
@blue: #ff9800;
@orange: #ff976a;
@orange-dark: #ed6a0c;
@orange-light: #fffbe8;
// @green: #07c160;
@green: #ff9800;
@gray: #9b9b9b;
@gray-light: #ccc;
@gray-darker: #888;
@gray-dark: #50596b;

// Gradient Colors
@gradient-red: linear-gradient(to right, #ff6034, #ee0a24);
@gradient-orange: linear-gradient(to right, #ffd01e, #ff8917);

// Component Colors
@text-color: #323233;
@active-color: #f2f3f5;
@active-opacity: 0.7;
@disabled-opacity: 0.5;
@background-color: #f8f8f8;
@background-color-light: #fafafa;

// box-show
@box-show: 0 1px 4px 1px rgba(255, 173, 0, 0.2);

// border
@border: #ebedf0;
